      h1 <i>Neuromarketing</i> en el turismo

    .row.justify-content-center.align-items-center.mb-5
      .col-10.col-lg-6
        figure
          img(src='@/assets/curso/temas/tema3/img1.svg', alt='Imgen decorativa')

      .col-lg-6
        p(data-aos="fade-right") La industria de los viajes y el turismo es la industria de las emociones debido a la cantidad de experiencias que produce, no solo durante el viaje, sino antes y despus del mismo. El turismo tiene la capacidad de generar recuerdos de toda una vida, ya que estos van ligados a las emociones y estas desempean un rol fundamental en la calificacin final del turista con respecto a un destino, sus servicios y su gente.

        .cajon.color-primario.p-4(data-aos="flip-down")
          p.mb-0 Cuando este tema se mezcla con mercadeo, se comienza a hablar de <i>neuromarketing</i>, que es la unin entre la ciencia y las tcnicas de <i>marketing</i>; es la herramienta que contribuye a comprender las emociones y las razones por las cuales viaja un turista. Desde el punto de vista cientfico, se puede decir que el <i>neuromarketing</i> tiene por objetivo entender el comportamiento de compra de un cliente con respecto a una marca, teniendo en cuenta la actividad neuronal.


    .row.justify-content-center.align-items-center.mb-5
      .tarjeta-amarilla3.px-5.py-3(data-aos="flip-down")
        .row.justify-content-center.align-items-center.mb-5
          .col-lg-8
            p.mb-lg-0.mt-lg-5 No es una herramienta exclusiva del turismo, ya que se aplica en muchos otros campos, sin embargo, en la industria de los viajes se puede aplicar por ejemplo para conocer la opinin de un cliente antes de lanzar un nuevo producto turstico o una experiencia turstica; tambin funciona para definir una estrategia de precios o para conocer el gusto del pblico con respecto a un diseo publicitario. 

          .col-8.col-lg-4
            figure.mb-0
              img(src='@/assets/curso/temas/tema3/img2.svg', alt='Imgen decorativa').img-tarjeta

    .row.justify-content-center.align-items-center.mb-5
      .col-lg-6
        p(data-aos="fade-right") Normalmente, existen otros mtodos de investigacin para conocer la percepcin del turista, entre ellos estn los grupos focales y las encuestas, sin embargo, estos mtodos no son del todo confiables, ya que las respuestas pueden estar alteradas por el propio turista a fin de agradar o dar la respuesta correcta. 

        p(data-aos="fade-right") En el cerebro, por el contrario, no hay mentiras, porque el inconsciente es quien finalmente domina los comportamientos; as pues, el <i>neuromarketing</i> busca identificar las reas del cerebro que se activan con la toma de decisiones de los turistas, evidenciando impulsos en los msculos del rostro, frecuencia en los latidos cardacos, sudor, lenguaje corporal, etc. 

        .cajon.color-azul1.p-4(data-aos="flip-down")
          p.mb-0 Con esta ciencia, se podran resolver interrogantes como: por qu el turista escogi el destino?, por qu le pareci atractivo tal hotel y no otro?, qu se podra hacer para que los turistas vuelvan a comprar el paquete turstico o la experiencia turstica?

      .col-10.col-lg-6
        figure
          img(src='@/assets/curso/temas/tema3/img3.svg', alt='Imgen decorativa')


    .bg1-tema1.mb-5
      .row.justify-content-center.align-items-center.mb-5
        p(data-aos="fade-right") Para determinar las respuestas emocionales y neuronales y conocer realmente lo que siente el turista con respecto a un servicio, destino o producto, el <i>neuromarketing</i> se basa en diferentes tcnicas como, por ejemplo, biosensores, resonancias magnticas, medicin del ritmo cardaco, electromiografa, <i>eyetracking</i>, y medicin de respuestas galvnicas. Se invita a revisar el anexo para diferenciar cada una de ellas.

      .row.justify-content-center.align-items-center.mb-5
        TabsC.color-primario
          .py-3.py-md-4(titulo="Biosensores")
            .row
              .col-md-5.mb-4.mb-md-0
                p Son pequeos dispositivos de naturaleza <i>wearable</i> o <i>wireless</i> capaces de cuantificar y medir determinadas sensaciones.

                p Las emociones de una persona son fcilmente identificables si se tienen en cuenta ciertas mtricas: la frecuencia cardiaca, el pulso, la sudoracin, la respiracin, entre otros.

                p Permiten identificar el impacto de un estmulo sobre una persona, mostrando los niveles de activacin de su cuerpo, por tanto son parte importante dentro de las tcnicas de investigacin del <i>neuromarketing.</i>

              .col-md-7
                figure
                  img(src='@/assets/curso/temas/tema3/img4.svg', alt='Imgen decorativa')

          .py-3.py-md-4(titulo="Electroencefalografa (EEG)")
            .row
              .col-md-5.mb-4.mb-md-0
                p El EGG determina los cambios qumicos o elctricos que se producen en el cerebro.

                p A travs  de un casco (existen modelos <i>wearables</i> muy cmodos) con sensores se recogen leves corrientes elctricas inducidas por la actividad cerebral.

                p As pues, un usuario conectado a un sistema EEG, al percibir un estmulo, el sistema muestra en qu zona del cerebro se ha llevado la mayor parte de la actividad.

                p Las tcnicas de electroencefalografa (EEG) trabajan bajo modelos predefinidos de aprendizaje de las emociones, ofreciendo respuestas verdaderamente interesantes.
                
              .col-md-7
                figure
                  img(src='@/assets/curso/temas/tema3/img5.svg', alt='Imgen decorativa')

          .py-3.py-md-4(titulo="Electromiografa")
            .row
              .col-md-5.mb-4.mb-md-0
                p Mide las reacciones de los msculos del cuerpo, especialmente los de la cara; esto es til para saber la aceptacin o rechazo de la persona sobre un producto, servicio, comercial, cua, etc.

                p Las expresiones faciales estn fuertemente vinculadas a las emociones, y de eso se trata, capturar las emociones y darles respuesta en una estrategia de <i>marketing.</i>

                p Esta tecnologa permite el reconocimiento automtico de emociones como sonrisa, tristeza, asco, sorpresa y  suelen basarse en el anlisis de los 120 puntos faciales ms caractersticos.
                
              .col-md-7
                figure
                  img(src='@/assets/curso/temas/tema3/img6.svg', alt='Imgen decorativa')

          .py-3.py-md-4(titulo="<i>Eye-tracking</i>")
            .row
              .col-md-5.mb-4.mb-md-0
                p Intenta comprender los mecanismos que guan a los consumidores para seleccionar ciertos puntos de inters ante una imagen con el seguimiento ocular para proporcionar informacin sobre qu es ms relevante para la atencin del usuario, basado en un sistema de patrones de fijacin visuales.

                p Existen <i>software</i> que permiten:

                ul.lista-ul--color.fa-ul2
                  li 
                    i.fas.fa-dot-circle(style="color: #EB6F2D")
                    | Medir el tamao de la pupila y la distancia entre la persona y el estmulo.
                  li 
                    i.fas.fa-dot-circle(style="color: #EB6F2D")
                    | Identificar el punto de fijacin (dnde mira la persona).
                  li 
                    i.fas.fa-dot-circle(style="color: #EB6F2D")
                    | Secuencia en la que los ojos cambian de foco.
                  li 
                    i.fas.fa-dot-circle(style="color: #EB6F2D")
                    | Nmero de parpadeos.

              .col-md-7
                figure
                  img(src='@/assets/curso/temas/tema3/img7.svg', alt='Imgen decorativa')

          .py-3.py-md-4(titulo="Medicin de respuestas galvnicas")
            .row
              .col-md-5.mb-4.mb-md-0
                p Lo que hace es medir las respuestas de la piel ante el estmulo, por ejemplo, la sudoracin. En el <i>marketing</i> se seala que si la persona, ante el estmulo, aumenta su sudoracin, indica que los niveles de estrs tambin aumentaron.

                p La secrecin de sudor y la conductancia de la piel son dos factores claves vinculados a las emociones. 

                p A travs de sensores y la monitorizacin de los mismos, es posible medir estos factores que dan un mensaje directo sobre las emociones del usuario.
                
              .col-md-7
                figure
                  img(src='@/assets/curso/temas/tema3/img8.svg', alt='Imgen decorativa')

    .row.justify-content-center.align-items-center.mb-5
      p(data-aos="fade-left") Luego de aprender las tcnicas, se ver, entonces, cmo se pueden incorporar las estrategias del <i>neuromarketing</i> a cualquier producto o servicio en turismo.

    .row.justify-content-center.align-items-center.mb-5
      .tarjeta.tarjeta--gris.p-5
        SlyderA(tipo="b")
          .row.justify-content-center
            .col-md-5.mb-4.mb-md-0
              h4 La mirada
              p La eficacia del contacto visual y del uso de personas en los anuncios son conclusiones ya antiguas en el medio del <i>marketing</i>. Pero lo que no se saba hasta hace poco es que cuando se usan imgenes de personas mirando fijamente al consumidor, la atencin se retira del anuncio y el foco queda solo en las imgenes.

              p Por eso, el <i>neuromarketing</i> recomienda el uso de imgenes de personas dirigiendo su mirada hacia un punto de inters. As, el anuncio se mantiene atractivo y la atencin del consumidor se vuelve hacia el objetivo de la campaa: el producto.


            .col-md-7
              figure
                img(src='@/assets/curso/temas/tema3/img9.svg', alt='Imgen decorativa')

          .row.justify-content-center
            .col-md-5.mb-4.mb-md-0
              h4 Los colores
              p Los colores influyen directamente en la forma que el cliente se siente. Cada color est vinculado a una emocin e insertar una tonalidad que entra en conflicto con el "humor" elegido para el anuncio puede arruinar el objetivo final.

              p Por lo tanto, utilizar un color de forma eficaz puede ser una poderosa herramienta de <i>marketing.</i> Especialistas en <i>neuromarketing</i> especializados en el color han dividido los colores en subgrupos como una gua de cmo se pueden utilizar de forma eficaz. Azul, por ejemplo, es el color recomendado si deseas atraer profesionales.


            .col-md-7
              figure
                img(src='@/assets/curso/temas/tema3/img10.svg', alt='Imgen decorativa')

          .row.justify-content-center
            .col-md-5.mb-4.mb-md-0
              h4 La velocidad/rapidez
              p La sensacin de velocidad, de agilidad, atrae al pblico. El sentimiento de seguridad y estabilidad, utilizado por empresas tradicionales, es efectivo para muchos segmentos. Pero lo que atrae al pblico efectivamente es el sentimiento de velocidad.

              p Las promesas de conveniencia, rapidez y eficiencia activan el cerebro de los consumidores a una escala mucho mayor que la seguridad.

            .col-md-7
              figure
                img(src='@/assets/curso/temas/tema3/img11.svg', alt='Imgen decorativa')

          .row.justify-content-center
            p.mb-5 Es decir, ante ciertos estmulos, el cerebro entiende que necesitas actuar rpidamente. Algunos de los principales son:
            .col-md-5.mb-4.mb-md-0
              h4 Prueba social

              p Presentar la cantidad de personas que visitan tu blog o ya han comprado tu producto va a motivar a quien lleg ahora para hacer lo mismo. Al final, tenemos la tendencia de seguir la mayora.


            .col-md-7
              figure
                img(src='@/assets/curso/temas/tema3/img12.svg', alt='Imgen decorativa')

          .row.justify-content-center
            .col-md-5.mb-4.mb-md-0
              h4 Escasez
              p Las ediciones limitadas son el ejemplo perfecto de cmo crear un efecto de escasez puede ser positivo. El cerebro entiende que necesita actuar rpido para no perder la oportunidad.

            .col-md-7
              figure
                img(src='@/assets/curso/temas/tema3/img13.svg', alt='Imgen decorativa')

          .row.justify-content-center
            .col-md-5.mb-4.mb-md-0
              h4 Urgencia
              p Siguiendo el mismo principio de la escasez, la urgencia lleva a la persona a actuar por impulso. Funciona? El Black Friday prueba que s.

            .col-md-7
              figure
                img(src='@/assets/curso/temas/tema3/img14.svg', alt='Imgen decorativa')

          .row.justify-content-center
            .col-md-5.mb-4.mb-md-0
              h4 Exclusividad

              p Las personas aman formar parte de grupos selectos o ganar acceso anticipado a productos y servicios, pues la sensacin de exclusividad las hace sentir importantes.

            .col-md-7
              figure
