    #t_3_1.titulo-segundo.color-acento-contenido(data-aos="fade-right")
       h2 3.1 Caractersticas

    .row.justify-content-center.align-items-center.mb-5
      .col-lg-7
        p(data-aos="fade-left") Se consideran caractersticas clave como son la atencin, emocin y memoria, elementos esenciales para entender la lgica de consumo, comprender los deseos, impulsos y motivaciones de los individuos a travs del estudio de las reacciones neurolgicas a ciertos estmulos. 

        p(data-aos="fade-right") Algunos expertos en el tema destacan que los tres factores anteriormente mencionados son los que ms influyen en las decisiones de compra. Algunas investigaciones hechas por neurlogos explican que la mayor parte de todas las decisiones de compra se toman en el nivel subconsciente y que, por lo tanto, es importante tomar en cuenta el tipo de recuerdo para poder resolver problemas de <i>marketing</i> y establecer acciones que permitan mejorar y alcanzar los distintos objetivos.

      .col-10.col-lg-5
        figure
          img(src='@/assets/curso/temas/tema3/img16.svg', alt='Imgen decorativa')

    .row.justify-content-center.align-items-center.mb-5
      p(data-aos="fade-right") Adicional a esos elementos, el <i>neuromarketing</i> cuenta con 10 caractersticas fundamentales:



    .row.justify-content-center.align-items-center.mb-5
      SlyderF.mb-5(columnas="col-md-6 col-xl-4")
        .tarjeta-avatar
          img(src='@/assets/curso/temas/tema3/img17.svg', alt='Imgen decorativa')
          .tarjeta.tarjeta--gris.p-4
            h4.text-center.mt-5 Cientificidad
            p.text-center Se identifican patrones de comportamiento mediante tcnicas cientficas.

        .tarjeta-avatar
          img(src='@/assets/curso/temas/tema3/img18.svg', alt='Imgen decorativa')
          .tarjeta.tarjeta--gris.p-4
            h4.text-center.mt-5 Dinamismo
            p.text-center La mente de una persona es movible, no siempre acta, piensa o reacciona de la misma manera; asimismo, ninguna persona es igual, as que el <i>neuromarketing</i> se adapta a ese dinamismo.

        .tarjeta-avatar
          img(src='@/assets/curso/temas/tema3/img19.svg', alt='Imgen decorativa')
          .tarjeta.tarjeta--gris.p-4
            h4.text-center.mt-5 Complejidad
            p.text-center Los procesos del cerebro siempre sern complejos. No hay manera de navegar en este sin enfrentarse a mltiples factores, lo cual vuelve sofisticado al <i>neuromarketing</i>.

        .tarjeta-avatar
          img(src='@/assets/curso/temas/tema3/img20.svg', alt='Imgen decorativa')
          .tarjeta.tarjeta--gris.p-4
            h4.text-center.mt-5 Variabilidad 
            p.text-center Los resultados, las mediciones e investigaciones varan de acuerdo con un contexto en particular.

        .tarjeta-avatar
          img(src='@/assets/curso/temas/tema3/img21.svg', alt='Imgen decorativa')
          .tarjeta.tarjeta--gris.p-4
            h4.text-center.mt-5 Enfoque
            p.text-center Las estrategias que deriven del <i>neuromarketing</i> tienen que enfocarse hacia algo; no son totalizadoras, sino especficas.

        .tarjeta-avatar
          img(src='@/assets/curso/temas/tema3/img22.svg', alt='Imgen decorativa')
          .tarjeta.tarjeta--gris.p-4
            h4.text-center.mt-5 Influencia
            p.text-center Los mltiples factores a los que est expuesto un individuo en su desarrollo representan influencias para sus comportamientos y hbitos, as que el <i>neuromarketing</i> debe considerar esto para tener un indicador ms certero.

        .tarjeta-avatar
          img(src='@/assets/curso/temas/tema3/img23.svg', alt='Imgen decorativa')
          .tarjeta.tarjeta--gris.p-4
            h4.text-center.mt-5 Temporalidad
            p.text-center Debe estar enmarcado en un tiempo.

        .tarjeta-avatar
          img(src='@/assets/curso/temas/tema3/img24.svg', alt='Imgen decorativa')
          .tarjeta.tarjeta--gris.p-4
            h4.text-center.mt-5 Causalidad
            p.text-center Entiende que cualquier reaccin de una persona es causal y no casual, as que llega al fondo para conocer el impulso primigenio.

        .tarjeta-avatar
          img(src='@/assets/curso/temas/tema3/img25.svg', alt='Imgen decorativa')
          .tarjeta.tarjeta--gris.p-4
            h4.text-center.mt-5 Precisin
            p.text-center Al usar tcnicas de la neurociencia es preciso en sus resultados.

        .tarjeta-avatar
          img(src='@/assets/curso/temas/tema3/img26.svg', alt='Imgen decorativa')
          .tarjeta.tarjeta--gris.p-4
            h4.text-center.mt-5 Prediccin
            p.text-center Con el <i>neuromarketing</i> se busca anticiparse, prever escenarios y hacer pronsticos de comportamiento.

