    #t_3_2.titulo-segundo.color-acento-contenido(data-aos="fade-right")
       h2 3.2 Tipos

    .row.justify-content-center.align-items-center.mb-5
      p(data-aos="fade-left") Para seguir ampliando el conocimiento sobre el tema, se exponen los diferentes tipos de <i>neuromarketing</i> que existen y algunos casos de xito en cada una de las categoras. 

    .row.mb-5(data-aos="fade-right")
      .col-auto
        .bg-titulo1.p-2
          h4.mx-2.mb-0 <i>Neuromarketing</i> visual

    .row.justify-content-center.align-items-center.mb-5
      .col-lg-6.mb-3
        figure
          img(src='@/assets/curso/temas/tema3/img27.svg', alt='Imgen decorativa')

      .col-lg-6.mb-3
        .cajon.color-primario.p-4.mb-3(data-aos="flip-down")
          p.mb-0 Es el que se basa en cmo percibimos nuestro alrededor con los ojos. Est demostrado que una imagen llega siempre de forma muchsimo ms rpida al cerebro, con lo cual, la eficacia del mensaje que se quiere transmitir, tambin est ms garantizada. Segn estudios, el cerebro retiene el 80 % de lo que ve, 20 % de lo que lee y 10 % de lo que escucha.

        p(data-aos="fade-left") Tambin hay varias tcnicas que se utilizan para hacer que el consumidor perciba algo mucho ms interesante, cmo terminar los precios en 0,99, haciendo que nuestro cerebro perciba que ese precio es ms bajo que otro. De igual manera, las palabras gratis, rebajas, ocasin, oferta, son otras tcnicas de venta para llamar la atencin de nuestros consumidores.

      .col-lg-6.mb-3
        h4(data-aos="fade-right") Ejemplo Frito-Lay 

        p(data-aos="fade-left") El caso de xito de Frito-Lay es uno de los ms conocidos en el rea del <i>neuromarketing</i>. Se trata de una compaa que ofrece snacks de todo tipo y muy reconocidos: Lay's, Fritos, Ruffles, Doritos, Cheetos Segn sus nmeros, los hombres consumen muchos ms snacks salados que las mujeres.

        p(data-aos="fade-right") La compaa deseaba saber cul era la razn por la que las mujeres no se decantaban por sus productos salados. As pues, se apoyaron en las tcnicas de <i>neuromarketing</i> para dar respuesta a esta cuestin.

        p(data-aos="fade-left") Los resultados fueron muy sorprendentes y llamativos:

      .col-lg-6.mb-3
        figure
          img(src='@/assets/curso/temas/tema3/img28.svg', alt='Imgen decorativa')

      .col-lg-6.mb-3
        figure
          img(src='@/assets/curso/temas/tema3/img29.svg', alt='Imgen decorativa')

      .col-lg-6.mb-3
        .tarjeta-2.p-4.mb-3
          ul.lista-ul--color.fa-ul2
            li(data-aos="fade-up") 
              i.fas.fa-dot-circle(style="color: #EB6F2D")
              | En primer lugar, se descubri que el centro de comunicacin en el cerebro de las mujeres estaba ms desarrollado que en el de los hombres, lo que concluy que las mujeres pueden procesar anuncios o mensajes con ms complejidad.
            li(data-aos="fade-up") 
              i.fas.fa-dot-circle(style="color: #EB6F2D")
              | El centro emocional y la memoria (hipocampo) tambin destacaban de manera importante, deduciendo que las mujeres buscaran personajes con los que pudieran identificarse.
            li(data-aos="fade-up")
              i.fas.fa-dot-circle(style="color: #EB6F2D")
              | El sentimiento de culpa fue uno de los elementos ms esclarecedores de este estudio.

    .row.mb-5(data-aos="fade-right")
      .col-auto
        .bg-titulo1.p-2
          h4.mx-2.mb-0 <i>Neuromarketing</i> auditivo

    .row.justify-content-center.align-items-center.mb-5
      .col-lg-6
        p(data-aos="fade-left") Es el que se basa en todo aquello que percibe nuestro sentido de la audicin, todo aquello que escuchamos. No olvidemos pues, que nuestro sentido del odo tiene memoria auditiva y esta es la que almacena informacin sobre nuestras experiencias a lo largo de nuestra vida, tanto en el pasado como en el presente.

        p(data-aos="fade-right") Cada uno de nosotros somos ms sensibles a la msica u otros sonidos, incluso al silencio, motivo por el cual, por ejemplo, los spots tienden a hacer uso de esos datos para potenciar una determinada msica o hacer un silencio en un momento concreto de la publicidad. 

        h4(data-aos="fade-left") Ejemplo Fan Mallorca Shopping

        .cajon.color-azul1.p-4.mb-3(data-aos="flip-down")
          p En 2006 se llev a cabo un estudio en la seccin de vinos de una tienda, donde se exhiban botellas de vino francs y alemn, de sabor y precio similar.

          p.mb-0 En los das de la semana en que se tocaba msica de acorden francs, el 77 % del vino vendido era francs, mientras que en los das de msica alemana, el 73 % del vino vendido era alemn. Lo ms curioso de este experimento es que el 95 % de la gente que haba comprado vino no se haba percatado del tipo de msica ambiental.

      .col-10.col-lg-6
        figure
          img(src='@/assets/curso/temas/tema3/img30.svg', alt='Imgen decorativa')

    .row.mb-5(data-aos="fade-right")
      .col-auto
        .bg-titulo1.p-2
          h4.mx-2.mb-0 <i>Neuromarketing</i> kinestsico

    .row.justify-content-center.align-items-center.mb-5
      .col-lg-6.mb-3
        p(data-aos="fade-left") Se basa en lo que percibimos con el sentido del tacto, el olfato y el gusto. A travs de dichas sensaciones, un consumidor, o su cerebro ms bien, determinar en su subconsciente si ese producto llegar a su casa o no. Si bien es menos utilizado, no es menos importante que los otros dos. 

        .cajon.color-primario.p-4.mb-3(data-aos="flip-down")
          p.mb-0 Ejemplos en acciones podran ser las degustaciones de productos y presentaciones donde los consumidores y pblico pueden oler, tocar y probar.

      .col-lg-6.mb-3
        figure
          img(src='@/assets/curso/temas/tema3/img31.svg', alt='Imgen decorativa')

      .col-lg-6.mb-3
