          p(data-aos="fade-left") Todas las tecnologas que se usan hoy da para realizar anlisis de <i>neuromarketing</i> fueron aplicadas desde aos atrs con la intencin de conocer el comportamiento del pblico con respecto a una problemtica x. Es as como en los aos 60, el investigador y psiclogo Herbert Krugman, comenz con la utilizacin del pupilmetro en fines comerciales para indagar acerca del inters del ser humano con respecto a lo que vean en televisin. Durante esos aos, tambin se iniciaron experimentos para conocer las reacciones emocionales del pblico, a travs del Galvanic Skin Response (GSR).

